<template>
  <div class="container">
    <h1>Latest Posts</h1>
    <div class="create-post">
      <div>
        <label for="create-post">Say Something...</label>
        <input type="text" id="create-post" v-model="text" placeholder="Create a post">
        <button v-on:click="createPost">Create</button>
      </div>
      <div>
        <label for="sort">Sort By</label>
        <select id="sort" v-model="sort" @change="onChange($event)">
          <option disabled value="" selected hidden>Order</option>
          <option value="descending">New</option>
          <option value="ascending">Old</option>
        </select>
      </div>
    </div>
    <hr />
    <p class="error" v-if="error">{{ error }}</p>
    <div class="post-container">
      <div class="post" v-for="(post, index) in posts" v-bind:item="post" v-bind:index="index" v-bind:key="post._id">
        <p class="created-at">
          {{`${post.createdAt.getDate()}/${post.createdAt.getMonth() + 1}/${post.createdAt.getFullYear()} -
          ${post.createdAt.toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'})}`}}</p>
        <p class="text">
          {{ post.text }}
        </p>
        <p class="delete-button" v-on:click="deletePost(post._id)">Delete</p>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from "../PostService";

export default {
  name: "PostComponent",
  data() {
    return {
      posts: [],
      error: "",
      text: "",
      sort: "",
    };
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch (e) {
      this.error = e;
    }
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
      this.text = "";
      this.sortPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
      this.sortPosts();
    },
    async onChange(event) {
      this.sort = event.target.value;
      this.sortPosts();
    },
    sortPosts() {
      this.posts = this.posts.sort((a, b) => {
        if (this.sort === "descending") {
          return new Date(b.createdAt) - new Date(a.createdAt);
        } else {
          return new Date(a.createdAt) - new Date(b.createdAt);
        }
      });
    },
  },
};
</script>

